@mixin font-family() {
    font-family: "SourceHanSerifTW", "Noto Serif TC", sans-serif, Arial, "Microsoft JhengHei", Helvetica;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-ExtraLight.otf") format("opentype");
    font-weight: 200;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-Light.otf") format("opentype");
    font-weight: 300;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-Regular.otf") format("opentype");
    font-weight: 400;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-Medium.otf") format("opentype");
    font-weight: 500;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-SemiBold.otf") format("opentype");
    font-weight: 600;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-Bold.otf") format("opentype");
    font-weight: 700;
}

@font-face {
    font-family: "SourceHanSerifTW";
    src: url("../font/SourceHanSerifTC-Heavy.otf") format("opentype");
    font-weight: 900;
}