@import "./_variables";
@import "./_mixins";

.flow {
    padding: 60px 48px 73px;

    &-back {

        button,
        a {
            position: relative;
            z-index: 100;
            display: inline-flex;
            align-items: center;
            gap: 42px;
            font-size: 60px;
            font-weight: 600;
            line-height: 1.2;
            letter-spacing: 1.2px;
            text-decoration: none;
            color: #6a5853;

            span {
                margin-top: -2px;
            }

            &::before {
                content: '';
                width: 39px;
                height: 70px;
                background: url(../image/icon/back.svg) no-repeat center/contain;
            }

            .-menuOpen & {
                z-index: 0;
            }
        }
    }

    &-title {
        margin-top: 80px;
        font-size: 48px;
        font-weight: 600;
        letter-spacing: 1px;
        text-align: center;
        color: #483629;
    }

    &-card {
        margin: 57px 0 60px;
    }

    &-delete {
        text-align: center;
    }

    &-slider {
        position: relative;

        &>div {
            width: 796px;
            margin: 0 auto;
            overflow: hidden;
        }
    }

    &-action {
        margin-top: 60px;
        text-align: center;
    }

    @include breakpoint-down('tablet') {
        padding: vw(60) vw(48) vw(73);

        &-back {

            button,
            a {
                gap: vw(42);
                font-size: vw(60);
                letter-spacing: vw(1.2);

                span {
                    margin-top: vw(-2);
                }

                &::before {
                    width: vw(39);
                    height: vw(70);
                }
            }
        }

        &-title {
            margin-top: vw(80);
            font-size: vw(48);
            letter-spacing: vw(1);
        }

        &-card {
            margin: vw(57) 0 vw(60);
        }

        &-slider {
            &>div {
                width: vw(796);
            }
        }

        &-action {
            margin-top: vw(60);
        }
    }
}

.card {
    position: relative;
    width: 792px;
    margin: 0 auto;
    border: 2px solid #fff;

    &::after {
        content: 'loading...';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 40px;
        font-weight: 600;
        background-color: #ccc;
        backdrop-filter: blur(10px);
        opacity: 0;
        transition: opacity .3s;
        pointer-events: none;
    }

    &-bg {
        img {
            width: 100%;
        }
    }

    &-input {
        position: absolute;
        top: 87px;
        right: 64px;
        box-sizing: border-box;
        width: 470px;
        height: 752px;
        padding: 5px;

        &>span {
            position: absolute;
            width: 13px;
            height: 13px;
            background-color: #63a378;
            z-index: 1;

            &:nth-child(1) {
                top: 0;
                left: 0;
            }

            &:nth-child(2) {
                top: 0;
                right: 0;
            }

            &:nth-child(3) {
                bottom: 0;
                left: 0;
            }

            &:nth-child(4) {
                bottom: 0;
                right: 0;
            }
        }
    }

    &.-loading {
        &::after {
            opacity: 1;
            pointer-events: visible;
        }
    }

    @include breakpoint-down('tablet') {
        width: vw(792);
        border-width: 1px;

        &::after {
            font-size: vw(40);
            backdrop-filter: blur(vw(10));
        }


        &-input {
            top: vw(87);
            right: vw(64);
            width: vw(470);
            height: vw(752);
            padding: vw(5);

            &>span {
                width: vw(13);
                height: vw(13);
            }
        }
    }
}

.textarea {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 30px 30px 30px 60px;
    font-size: 40px;
    font-weight: 600;
    line-height: 1.8;
    letter-spacing: 0.8px;
    color: #6a5853;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    word-wrap: break-word;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%2363A378FF' stroke-width='3' stroke-dasharray='8%2c 4' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");

    &-wrap {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    &-input {
        position: relative;
        min-width: 100%;
        height: 100%;
        white-space: pre-wrap;
        outline: none;

        &::after {
            content: '請在框內寫下感謝祝福';
            position: absolute;
            top: 0;
            right: 0;
            // color: rgba(99, 163, 120, 0.5);
            color: red;
            pointer-events: none;
        }
    }

    &.-hasText & {
        &-input {
            &::after {
                content: none;
            }
        }
    }

    &.-over:not(.-focus) {
        &::after {
            content: '...';
            position: absolute;
            top: 30px;
            left: 30px;
        }
    }

    &.-focus {
        padding: 30px 30px 30px 40px;
    }

    &.-focus & {
        &-wrap {
            overflow: auto;
        }
    }

    @include breakpoint-down('tablet') {
        padding: vw(30) vw(30) vw(30) vw(60);
        font-size: vw(40);
        letter-spacing: vw(0.8);

        &.-over:not(.-focus) {
            &::after {
                top: vw(30);
                left: vw(30);
            }
        }

        &.-focus {
            padding: vw(30) vw(30) vw(30) vw(40);
        }
    }
}

.swiper {
    &[data-slider-bg] {
        margin: 0 0 0 -642px;
    }

    &[data-slider-bg] & {
        &-slide {
            box-sizing: border-box;
            width: 214px;
            padding: 0 30px;
        }
    }

    &-button-prev,
    &-button-next {
        width: 120px;
        height: 120px;
        margin: 0;
        background: url(../image/icon/next.svg) no-repeat center/contain;
        outline: none;

        &.swiper-button-disabled {
            opacity: 0;
        }
    }

    &-button-prev {
        left: 0;
        transform: translateY(-50%) rotateY(180deg);
    }

    &-button-next {
        right: 0;
        transform: translateY(-50%);
    }

    @include breakpoint-down('tablet') {
        &[data-slider-bg] {
            margin: 0 0 0 vw(-642);
        }

        &[data-slider-bg] & {
            &-slide {
                width: vw(214);
                padding: 0 vw(30);
            }
        }

        &-button-prev,
        &-button-next {
            width: vw(120);
            height: vw(120);
        }
    }
}

.cardBg {
    display: block;

    input {
        display: none;
    }

    img {
        box-sizing: border-box;
        width: 100%;
        border: 2px solid #fff;
        opacity: 0.3;
        transition: opacity .3s;
    }

    input:checked+img {
        opacity: 1;
    }

    @include breakpoint-down('tablet') {
        img {
            border-width: 1px;
        }
    }
}